// å›¾æ ‡çŠ¶æ€å¸¸é‡
export const TileState = {
  NORMAL: 'normal',       // æ­£å¸¸çŠ¶æ€
  SELECTED: 'selected',   // è¢«é€‰ä¸­
  MATCHED: 'matched',     // å·²åŒ¹é…å¾…æ¶ˆé™¤
  FALLING: 'falling',     // ä¸‹è½ä¸­
  SPAWNING: 'spawning'    // ç”Ÿæˆä¸­
};

// ç‰¹æ®Šå›¾æ ‡ç±»å‹å¸¸é‡
export const SpecialTileType = {
  NONE: 'none',           // æ™®é€šå›¾æ ‡
  BOMB: 'bomb',           // ç‚¸å¼¹ï¼ˆ4è¿ç”Ÿæˆï¼‰
  COLOR_BOMB: 'color_bomb', // å½©è‰²ç‚¸å¼¹ï¼ˆ5è¿ç”Ÿæˆï¼‰
  ROW_CLEAR: 'row_clear',   // æ¨ªå‘æ¶ˆé™¤ï¼ˆL/Tå‹ç”Ÿæˆï¼‰
  COL_CLEAR: 'col_clear'    // çºµå‘æ¶ˆé™¤ï¼ˆL/Tå‹ç”Ÿæˆï¼‰
};

/**
 * å›¾æ ‡ç±»
 */
export class Tile {
  /**
   * åˆ›å»ºå›¾æ ‡
   * @param {number} type - å›¾æ ‡ç±»å‹ (0-4)
   * @param {number} x - ç½‘æ ¼Xåæ ‡
   * @param {number} y - ç½‘æ ¼Yåæ ‡
   */
  constructor(type, x, y) {
    this.type = type;                           // å›¾æ ‡ç±»å‹
    this.x = x;                                 // ç½‘æ ¼Xåæ ‡
    this.y = y;                                 // ç½‘æ ¼Yåæ ‡
    this.id = Tile.generateId();                // å”¯ä¸€æ ‡è¯†
    this.state = TileState.NORMAL;              // å½“å‰çŠ¶æ€
    this.isSpecial = false;                     // æ˜¯å¦ä¸ºç‰¹æ®Šå›¾æ ‡
    this.specialType = SpecialTileType.NONE;    // ç‰¹æ®Šå›¾æ ‡ç±»å‹
  }

  /**
   * ç”Ÿæˆå”¯ä¸€ID
   * @returns {string} å”¯ä¸€ID
   */
  static generateId() {
    return `tile_${Date.now()}_${Math.random().toString(36).substr(2, 9)}`;
  }

  /**
   * è®¾ç½®ä½ç½®
   * @param {number} x - ç½‘æ ¼Xåæ ‡
   * @param {number} y - ç½‘æ ¼Yåæ ‡
   */
  setPosition(x, y) {
    this.x = x;
    this.y = y;
  }

  /**
   * è®¾ç½®çŠ¶æ€
   * @param {string} state - æ–°çŠ¶æ€
   */
  setState(state) {
    if (!Object.values(TileState).includes(state)) {
      console.warn(`æ— æ•ˆçš„å›¾æ ‡çŠ¶æ€: ${state}`);
      return;
    }
    this.state = state;
  }

  /**
   * è®¾ç½®ä¸ºç‰¹æ®Šå›¾æ ‡
   * @param {string} specialType - ç‰¹æ®Šå›¾æ ‡ç±»å‹
   */
  setSpecial(specialType) {
    if (!Object.values(SpecialTileType).includes(specialType)) {
      console.warn(`æ— æ•ˆçš„ç‰¹æ®Šå›¾æ ‡ç±»å‹: ${specialType}`);
      return;
    }
    this.isSpecial = true;
    this.specialType = specialType;
  }

  /**
   * é‡ç½®ä¸ºæ™®é€šå›¾æ ‡
   */
  resetSpecial() {
    this.isSpecial = false;
    this.specialType = SpecialTileType.NONE;
  }

  /**
   * æ£€æŸ¥æ˜¯å¦ä¸ºæ™®é€šå›¾æ ‡
   * @returns {boolean}
   */
  isNormal() {
    return !this.isSpecial;
  }

  /**
   * å…‹éš†å›¾æ ‡
   * @returns {Tile} æ–°çš„å›¾æ ‡å®ä¾‹
   */
  clone() {
    const cloned = new Tile(this.type, this.x, this.y);
    cloned.state = this.state;
    cloned.isSpecial = this.isSpecial;
    cloned.specialType = this.specialType;
    return cloned;
  }

  /**
   * è½¬æ¢ä¸ºå­—ç¬¦ä¸²ï¼ˆç”¨äºè°ƒè¯•ï¼‰
   * @returns {string}
   */
  toString() {
    const special = this.isSpecial ? ` [${this.specialType}]` : '';
    return `Tile(type=${this.type}, pos=(${this.x},${this.y}), state=${this.state}${special})`;
  }

  /**
   * è½¬æ¢ä¸ºJSONå¯¹è±¡
   * @returns {Object}
   */
  toJSON() {
    return {
      id: this.id,
      type: this.type,
      x: this.x,
      y: this.y,
      state: this.state,
      isSpecial: this.isSpecial,
      specialType: this.specialType
    };
  }
}

export default Tile;
