/**
 * åŒ¹é…æ£€æµ‹å™¨ - æ£€æµ‹æ¸¸æˆæ¿ä¸Šçš„åŒ¹é…æ¨¡å¼
 */

/**
 * åŒ¹é…ç±» - å­˜å‚¨åŒ¹é…ä¿¡æ¯
 */
export class Match {
  /**
   * åˆ›å»ºåŒ¹é…å¯¹è±¡
   * @param {Array<Tile>} tiles - åŒ¹é…çš„å›¾æ ‡æ•°ç»„
   * @param {string} direction - åŒ¹é…æ–¹å‘ ('horizontal' | 'vertical')
   */
  constructor(tiles, direction) {
    this.tiles = tiles;           // åŒ¹é…çš„å›¾æ ‡æ•°ç»„
    this.direction = direction;   // åŒ¹é…æ–¹å‘
    this.length = tiles.length;   // åŒ¹é…é•¿åº¦
  }

  /**
   * è·å–åŒ¹é…çš„ä½ç½®æ•°ç»„
   * @returns {Array<{x, y}>}
   */
  getPositions() {
    return this.tiles.map(tile => ({ x: tile.x, y: tile.y }));
  }

  /**
   * è·å–åŒ¹é…çš„ç±»å‹
   * @returns {number}
   */
  getType() {
    return this.tiles.length > 0 ? this.tiles[0].type : -1;
  }
}

/**
 * åŒ¹é…æ£€æµ‹å™¨ç±»
 */
export class MatchDetector {
  constructor() {
    // ç¼“å­˜ç›¸å…³
    this.validMovesCache = null;
    this.boardStateHash = null;
  }

  /**
   * æŸ¥æ‰¾æ‰€æœ‰åŒ¹é…
   * @param {BoardManager} board - æ¸¸æˆæ¿
   * @returns {Array<Match>} åŒ¹é…æ•°ç»„
   */
  findMatches(board) {
    const matches = [];
    const matchedTiles = new Set();  // ç”¨äºå»é‡

    // æŸ¥æ‰¾æ¨ªå‘åŒ¹é…
    const horizontalMatches = this.findHorizontalMatches(board);
    horizontalMatches.forEach(match => {
      matches.push(match);
      match.tiles.forEach(tile => matchedTiles.add(`${tile.x},${tile.y}`));
    });

    // æŸ¥æ‰¾çºµå‘åŒ¹é…
    const verticalMatches = this.findVerticalMatches(board);
    verticalMatches.forEach(match => {
      matches.push(match);
      match.tiles.forEach(tile => matchedTiles.add(`${tile.x},${tile.y}`));
    });

    return matches;
  }

  /**
   * æŸ¥æ‰¾æ¨ªå‘åŒ¹é…
   * @param {BoardManager} board - æ¸¸æˆæ¿
   * @returns {Array<Match>} æ¨ªå‘åŒ¹é…æ•°ç»„
   */
  findHorizontalMatches(board) {
    const matches = [];

    for (let y = 0; y < board.rows; y++) {
      let currentType = -1;
      let currentMatch = [];

      for (let x = 0; x < board.cols; x++) {
        const tile = board.getTile(x, y);

        // âœ… è·³è¿‡ç©ºä½å’Œç‰¹æ®Šå›¾æ ‡ï¼ˆç‰¹æ®Šå›¾æ ‡ä¸å‚ä¸åŒ¹é…ï¼‰
        if (!tile || tile.isSpecial) {
          // é‡åˆ°ç©ºä½æˆ–ç‰¹æ®Šå›¾æ ‡ï¼Œæ£€æŸ¥å½“å‰åŒ¹é…
          if (currentMatch.length >= 3) {
            matches.push(new Match(currentMatch, 'horizontal'));
          }
          currentMatch = [];
          currentType = -1;
          continue;
        }

        if (tile.type === currentType) {
          // ç›¸åŒç±»å‹ï¼Œæ·»åŠ åˆ°å½“å‰åŒ¹é…
          currentMatch.push(tile);
        } else {
          // ä¸åŒç±»å‹ï¼Œæ£€æŸ¥å½“å‰åŒ¹é…
          if (currentMatch.length >= 3) {
            matches.push(new Match(currentMatch, 'horizontal'));
          }
          // å¼€å§‹æ–°çš„åŒ¹é…
          currentMatch = [tile];
          currentType = tile.type;
        }
      }

      // æ£€æŸ¥è¡Œæœ«çš„åŒ¹é…
      if (currentMatch.length >= 3) {
        matches.push(new Match(currentMatch, 'horizontal'));
      }
    }

    return matches;
  }

  /**
   * æŸ¥æ‰¾çºµå‘åŒ¹é…
   * @param {BoardManager} board - æ¸¸æˆæ¿
   * @returns {Array<Match>} çºµå‘åŒ¹é…æ•°ç»„
   */
  findVerticalMatches(board) {
    const matches = [];

    for (let x = 0; x < board.cols; x++) {
      let currentType = -1;
      let currentMatch = [];

      for (let y = 0; y < board.rows; y++) {
        const tile = board.getTile(x, y);

        // âœ… è·³è¿‡ç©ºä½å’Œç‰¹æ®Šå›¾æ ‡ï¼ˆç‰¹æ®Šå›¾æ ‡ä¸å‚ä¸åŒ¹é…ï¼‰
        if (!tile || tile.isSpecial) {
          // é‡åˆ°ç©ºä½æˆ–ç‰¹æ®Šå›¾æ ‡ï¼Œæ£€æŸ¥å½“å‰åŒ¹é…
          if (currentMatch.length >= 3) {
            matches.push(new Match(currentMatch, 'vertical'));
          }
          currentMatch = [];
          currentType = -1;
          continue;
        }

        if (tile.type === currentType) {
          // ç›¸åŒç±»å‹ï¼Œæ·»åŠ åˆ°å½“å‰åŒ¹é…
          currentMatch.push(tile);
        } else {
          // ä¸åŒç±»å‹ï¼Œæ£€æŸ¥å½“å‰åŒ¹é…
          if (currentMatch.length >= 3) {
            matches.push(new Match(currentMatch, 'vertical'));
          }
          // å¼€å§‹æ–°çš„åŒ¹é…
          currentMatch = [tile];
          currentType = tile.type;
        }
      }

      // æ£€æŸ¥åˆ—æœ«çš„åŒ¹é…
      if (currentMatch.length >= 3) {
        matches.push(new Match(currentMatch, 'vertical'));
      }
    }

    return matches;
  }

  /**
   * å¿«é€Ÿæ£€æŸ¥æŒ‡å®šä½ç½®æ˜¯å¦æœ‰åŒ¹é…ï¼ˆåªæ£€æŸ¥è¯¥ä½ç½®ï¼Œä¸æ‰«ææ•´ä¸ªæ£‹ç›˜ï¼‰
   * @param {BoardManager} board - æ¸¸æˆæ¿
   * @param {number} x - Xåæ ‡
   * @param {number} y - Yåæ ‡
   * @returns {boolean} æ˜¯å¦æœ‰åŒ¹é…
   */
  checkMatchAtPosition(board, x, y) {
    const tile = board.getTile(x, y);
    // âœ… ç‰¹æ®Šå›¾æ ‡ä¸å‚ä¸åŒ¹é…
    if (!tile || tile.isSpecial) return false;

    // æ£€æŸ¥æ¨ªå‘åŒ¹é…
    let horizontalCount = 1;
    
    // å‘å·¦æ£€æŸ¥
    for (let i = x - 1; i >= 0; i--) {
      const leftTile = board.getTile(i, y);
      // âœ… è·³è¿‡ç‰¹æ®Šå›¾æ ‡
      if (leftTile && !leftTile.isSpecial && leftTile.type === tile.type) {
        horizontalCount++;
      } else {
        break;
      }
    }
    
    // å‘å³æ£€æŸ¥
    for (let i = x + 1; i < board.cols; i++) {
      const rightTile = board.getTile(i, y);
      // âœ… è·³è¿‡ç‰¹æ®Šå›¾æ ‡
      if (rightTile && !rightTile.isSpecial && rightTile.type === tile.type) {
        horizontalCount++;
      } else {
        break;
      }
    }
    
    if (horizontalCount >= 3) return true;

    // æ£€æŸ¥çºµå‘åŒ¹é…
    let verticalCount = 1;
    
    // å‘ä¸Šæ£€æŸ¥
    for (let i = y - 1; i >= 0; i--) {
      const upTile = board.getTile(x, i);
      // âœ… è·³è¿‡ç‰¹æ®Šå›¾æ ‡
      if (upTile && !upTile.isSpecial && upTile.type === tile.type) {
        verticalCount++;
      } else {
        break;
      }
    }
    
    // å‘ä¸‹æ£€æŸ¥
    for (let i = y + 1; i < board.rows; i++) {
      const downTile = board.getTile(x, i);
      // âœ… è·³è¿‡ç‰¹æ®Šå›¾æ ‡
      if (downTile && !downTile.isSpecial && downTile.type === tile.type) {
        verticalCount++;
      } else {
        break;
      }
    }
    
    if (verticalCount >= 3) return true;

    return false;
  }

  /**
   * æ£€æŸ¥æ˜¯å¦æœ‰æœ‰æ•ˆç§»åŠ¨ï¼ˆä¼˜åŒ–ç‰ˆæœ¬ï¼Œæå‰ç»ˆæ­¢ + ç¼“å­˜ï¼‰
   * @param {BoardManager} board - æ¸¸æˆæ¿
   * @returns {boolean} æ˜¯å¦æœ‰æœ‰æ•ˆç§»åŠ¨
   */
  hasValidMoves(board) {
    // æ£€æŸ¥ç¼“å­˜
    const boardHash = this.getBoardHash(board);
    if (this.validMovesCache !== null && this.boardStateHash === boardHash) {
      return this.validMovesCache;
    }

    const { rows, cols } = board;

    // éå†æ‰€æœ‰å¯èƒ½çš„äº¤æ¢
    for (let y = 0; y < rows; y++) {
      for (let x = 0; x < cols; x++) {
        const tile = board.getTile(x, y);
        if (!tile) continue;

        // åªæ£€æŸ¥å³ä¾§å’Œä¸‹æ–¹çš„äº¤æ¢ï¼ˆé¿å…é‡å¤æ£€æŸ¥ï¼‰
        const adjacentPositions = [
          { x: x + 1, y: y },     // å³
          { x: x, y: y + 1 }      // ä¸‹
        ];

        for (const adj of adjacentPositions) {
          if (!board.isValidPosition(adj.x, adj.y)) continue;

          const adjTile = board.getTile(adj.x, adj.y);
          if (!adjTile) continue;

          // æ¨¡æ‹Ÿäº¤æ¢
          board.swapTiles({ x, y }, adj);

          // å¿«é€Ÿæ£€æŸ¥ï¼šåªæ£€æŸ¥äº¤æ¢çš„ä¸¤ä¸ªä½ç½®å‘¨å›´æ˜¯å¦äº§ç”ŸåŒ¹é…
          const hasMatch = this.checkMatchAtPosition(board, x, y) ||
                          this.checkMatchAtPosition(board, adj.x, adj.y);

          // äº¤æ¢å›æ¥
          board.swapTiles({ x, y }, adj);

          if (hasMatch) {
            // ç¼“å­˜ç»“æœ
            this.validMovesCache = true;
            this.boardStateHash = boardHash;
            return true; // æ‰¾åˆ°æœ‰æ•ˆç§»åŠ¨ï¼Œç«‹å³è¿”å›
          }
        }
      }
    }

    // ç¼“å­˜ç»“æœ
    this.validMovesCache = false;
    this.boardStateHash = boardHash;
    return false; // æ— æœ‰æ•ˆç§»åŠ¨
  }

  /**
   * æŸ¥æ‰¾æ‰€æœ‰å¯èƒ½çš„ç§»åŠ¨
   * @param {BoardManager} board - æ¸¸æˆæ¿
   * @returns {Array<{from, to}>} å¯èƒ½çš„ç§»åŠ¨æ•°ç»„
   */
  findPossibleMoves(board) {
    const possibleMoves = [];
    const { rows, cols } = board;

    for (let y = 0; y < rows; y++) {
      for (let x = 0; x < cols; x++) {
        const tile = board.getTile(x, y);
        if (!tile) continue;

        // æ£€æŸ¥å³ä¾§å’Œä¸‹æ–¹çš„äº¤æ¢
        const adjacentPositions = [
          { x: x + 1, y: y },
          { x: x, y: y + 1 }
        ];

        for (const adj of adjacentPositions) {
          if (!board.isValidPosition(adj.x, adj.y)) continue;

          const adjTile = board.getTile(adj.x, adj.y);
          if (!adjTile) continue;

          // æ¨¡æ‹Ÿäº¤æ¢
          board.swapTiles({ x, y }, adj);

          // æ£€æŸ¥æ˜¯å¦äº§ç”ŸåŒ¹é…
          const hasMatch = this.checkMatchAtPosition(board, x, y) ||
                          this.checkMatchAtPosition(board, adj.x, adj.y);

          // äº¤æ¢å›æ¥
          board.swapTiles({ x, y }, adj);

          if (hasMatch) {
            possibleMoves.push({
              from: { x, y },
              to: adj
            });
          }
        }
      }
    }

    return possibleMoves;
  }

  /**
   * ç”Ÿæˆæ¸¸æˆæ¿å“ˆå¸Œå€¼ï¼ˆç”¨äºç¼“å­˜ï¼‰
   * @param {BoardManager} board - æ¸¸æˆæ¿
   * @returns {string} å“ˆå¸Œå€¼
   */
  getBoardHash(board) {
    let hash = '';
    for (let y = 0; y < board.rows; y++) {
      for (let x = 0; x < board.cols; x++) {
        const tile = board.getTile(x, y);
        hash += tile ? tile.type : '-';
      }
    }
    return hash;
  }

  /**
   * æ¸…é™¤ç¼“å­˜ï¼ˆåœ¨æ¸¸æˆæ¿å˜åŒ–æ—¶è°ƒç”¨ï¼‰
   */
  clearCache() {
    this.validMovesCache = null;
    this.boardStateHash = null;
  }
}

export default MatchDetector;
