/**
 * è½»é‡çº§è¡¥é—´åŠ¨ç”»ç±»
 * ç”¨äºå®ç°ç²¾çµå±æ€§çš„å¹³æ»‘è¿‡æ¸¡
 */

import { Easing } from './Easing.js';

/**
 * è¡¥é—´åŠ¨ç”»ç±»
 */
export class Tween {
  /**
   * åˆ›å»ºè¡¥é—´åŠ¨ç”»
   * @param {Object} target - ç›®æ ‡å¯¹è±¡ï¼ˆé€šå¸¸æ˜¯ PixiJS Spriteï¼‰
   * @param {Object} props - è¦è¡¥é—´çš„å±æ€§ {x: 100, y: 200, alpha: 0.5, 'scale.x': 1.0, ...}
   * @param {number} duration - åŠ¨ç”»æ—¶é•¿ï¼ˆæ¯«ç§’ï¼‰
   * @param {string|Function} easing - ç¼“åŠ¨å‡½æ•°åç§°æˆ–è‡ªå®šä¹‰å‡½æ•°
   */
  constructor(target, props, duration, easing = 'linear') {
    this.init(target, props, duration, easing);
  }

  /**
   * åˆå§‹åŒ–æˆ–é‡æ–°åˆå§‹åŒ–è¡¥é—´åŠ¨ç”»ï¼ˆç”¨äºå¯¹è±¡æ± å¤ç”¨ï¼‰
   * @param {Object} target - ç›®æ ‡å¯¹è±¡
   * @param {Object} props - è¦è¡¥é—´çš„å±æ€§
   * @param {number} duration - åŠ¨ç”»æ—¶é•¿ï¼ˆæ¯«ç§’ï¼‰
   * @param {string|Function} easing - ç¼“åŠ¨å‡½æ•°åç§°æˆ–è‡ªå®šä¹‰å‡½æ•°
   */
  init(target, props, duration, easing = 'linear') {
    this.target = target;
    this.startProps = {};
    this.endProps = {};
    this.propAccessors = {}; // å­˜å‚¨å±æ€§è®¿é—®å™¨
    this.duration = duration;
    this.elapsed = 0;
    this.isComplete = false;
    this.isPaused = false;
    
    // è§£æç¼“åŠ¨å‡½æ•°
    if (typeof easing === 'string') {
      this.easing = Easing[easing] || Easing.linear;
    } else if (typeof easing === 'function') {
      this.easing = easing;
    } else {
      this.easing = Easing.linear;
    }
    
    // è®°å½•èµ·å§‹å€¼å¹¶è§£æåµŒå¥—å±æ€§
    if (target && props) {
      for (let key in props) {
        const accessor = this._parsePropertyPath(target, key);
        if (accessor) {
          this.propAccessors[key] = accessor;
          this.startProps[key] = accessor.get();
          this.endProps[key] = props[key];
        } else {
          console.warn(`Tween: ç›®æ ‡å¯¹è±¡æ²¡æœ‰å±æ€§ "${key}"`);
        }
      }
    }
    
    // Promise æ”¯æŒ
    this._resolvePromise = null;
    this._rejectPromise = null;
    this.promise = new Promise((resolve, reject) => {
      this._resolvePromise = resolve;
      this._rejectPromise = reject;
    });
  }

  /**
   * è§£æå±æ€§è·¯å¾„ï¼ˆæ”¯æŒåµŒå¥—å±æ€§å¦‚ "scale.x"ï¼‰
   * @param {Object} target - ç›®æ ‡å¯¹è±¡
   * @param {string} path - å±æ€§è·¯å¾„
   * @returns {Object|null} å±æ€§è®¿é—®å™¨ {get, set} æˆ– null
   */
  _parsePropertyPath(target, path) {
    const parts = path.split('.');
    
    // æ£€æŸ¥è·¯å¾„æ˜¯å¦æœ‰æ•ˆ
    let current = target;
    for (let i = 0; i < parts.length - 1; i++) {
      if (current[parts[i]] === undefined) {
        return null;
      }
      current = current[parts[i]];
    }
    
    const lastPart = parts[parts.length - 1];
    if (current[lastPart] === undefined) {
      return null;
    }
    
    // è¿”å›è®¿é—®å™¨
    return {
      get: () => {
        let obj = target;
        for (let i = 0; i < parts.length - 1; i++) {
          obj = obj[parts[i]];
        }
        return obj[lastPart];
      },
      set: (value) => {
        let obj = target;
        for (let i = 0; i < parts.length - 1; i++) {
          obj = obj[parts[i]];
        }
        obj[lastPart] = value;
      }
    };
  }

  /**
   * æ›´æ–°è¡¥é—´åŠ¨ç”»
   * @param {number} deltaTime - å¸§é—´éš”æ—¶é—´ï¼ˆæ¯«ç§’ï¼‰
   * @returns {boolean} æ˜¯å¦å®Œæˆ
   */
  update(deltaTime) {
    if (this.isComplete || this.isPaused) {
      return this.isComplete;
    }
    
    this.elapsed += deltaTime;
    const progress = Math.min(this.elapsed / this.duration, 1);
    
    // âœ… å¦‚æœè¿›åº¦è¾¾åˆ° 1ï¼Œç›´æ¥è®¾ç½®æœ€ç»ˆå€¼ï¼Œä¸å†è®¡ç®—ç¼“åŠ¨
    if (progress >= 1) {
      this.isComplete = true;
      
      // âœ… ç¡®ä¿æœ€ç»ˆå€¼ç²¾ç¡®ï¼ˆä½¿ç”¨è®¿é—®å™¨ï¼‰
      for (let key in this.endProps) {
        const accessor = this.propAccessors[key];
        if (accessor) {
          accessor.set(this.endProps[key]);
        }
      }
      
      // è§£æ Promise
      if (this._resolvePromise) {
        this._resolvePromise(this.target);
      }
      
      return true;
    }
    
    // è®¡ç®—ç¼“åŠ¨è¿›åº¦
    const easedProgress = this.easing(progress);
    
    // æ›´æ–°ç›®æ ‡å¯¹è±¡çš„å±æ€§ï¼ˆä½¿ç”¨è®¿é—®å™¨ï¼‰
    for (let key in this.endProps) {
      const start = this.startProps[key];
      const end = this.endProps[key];
      const accessor = this.propAccessors[key];
      
      if (start !== undefined && end !== undefined && accessor) {
        const value = start + (end - start) * easedProgress;
        accessor.set(value);
      }
    }
    
    return this.isComplete;
  }

  /**
   * æš‚åœåŠ¨ç”»
   */
  pause() {
    this.isPaused = true;
  }

  /**
   * æ¢å¤åŠ¨ç”»
   */
  resume() {
    this.isPaused = false;
  }

  /**
   * åœæ­¢åŠ¨ç”»
   */
  stop() {
    this.isComplete = true;
    if (this._rejectPromise) {
      this._rejectPromise(new Error('Tween stopped'));
    }
  }

  /**
   * è·³åˆ°ç»“æŸçŠ¶æ€
   */
  finish() {
    this.elapsed = this.duration;
    this.update(0);
  }

  /**
   * è·å–å½“å‰è¿›åº¦ (0-1)
   */
  getProgress() {
    return Math.min(this.elapsed / this.duration, 1);
  }

  /**
   * æ£€æŸ¥æ˜¯å¦æ­£åœ¨è¿è¡Œ
   */
  isRunning() {
    return !this.isComplete && !this.isPaused;
  }
}

export default Tween;
