/**
 * ç²’å­ç±»
 */
export class Particle {
  /**
   * åˆ›å»ºç²’å­
   * @param {PIXI.Sprite} sprite - PixiJS ç²¾çµå¯¹è±¡
   */
  constructor(sprite) {
    this.sprite = sprite;
    this.velocity = { x: 0, y: 0 };
    this.acceleration = { x: 0, y: 0 };
    this.lifetime = 1.0;
    this.age = 0;
    this.initialScale = 1.0;
    this.initialAlpha = 1.0;
    this.isAlive = true;
  }

  /**
   * åˆå§‹åŒ–ç²’å­ï¼ˆç”¨äºå¯¹è±¡æ± å¤ç”¨ï¼‰
   * @param {Object} config - ç²’å­é…ç½®
   */
  init(config) {
    const {
      x = 0,
      y = 0,
      velocityX = 0,
      velocityY = 0,
      accelerationX = 0,
      accelerationY = 0,
      lifetime = 1.0,
      scale = 1.0,
      alpha = 1.0,
      tint = 0xFFFFFF
    } = config;

    this.sprite.position.set(x, y);
    this.velocity.x = velocityX;
    this.velocity.y = velocityY;
    this.acceleration.x = accelerationX;
    this.acceleration.y = accelerationY;
    this.lifetime = lifetime;
    this.age = 0;
    this.initialScale = scale;
    this.initialAlpha = alpha;
    this.sprite.scale.set(scale);
    this.sprite.alpha = alpha;
    this.sprite.tint = tint;
    this.sprite.visible = true;
    this.isAlive = true;
  }

  /**
   * æ›´æ–°ç²’å­çŠ¶æ€
   * @param {number} deltaTime - å¸§é—´éš”æ—¶é—´ï¼ˆç§’ï¼‰
   * @returns {boolean} æ˜¯å¦æ­»äº¡
   */
  update(deltaTime) {
    if (!this.isAlive) return true;

    // æ›´æ–°å¹´é¾„
    this.age += deltaTime;

    // æ£€æŸ¥æ˜¯å¦è¶…è¿‡ç”Ÿå‘½å‘¨æœŸ
    if (this.age >= this.lifetime) {
      this.isAlive = false;
      this.sprite.visible = false;
      return true;
    }

    // æ›´æ–°é€Ÿåº¦ï¼ˆåº”ç”¨åŠ é€Ÿåº¦ï¼‰
    this.velocity.x += this.acceleration.x * deltaTime;
    this.velocity.y += this.acceleration.y * deltaTime;

    // æ›´æ–°ä½ç½®
    this.sprite.x += this.velocity.x * deltaTime;
    this.sprite.y += this.velocity.y * deltaTime;

    // è®¡ç®—ç”Ÿå‘½å‘¨æœŸè¿›åº¦ï¼ˆ0-1ï¼‰
    const progress = this.age / this.lifetime;

    // æ›´æ–°é€æ˜åº¦ï¼ˆçº¿æ€§è¡°å‡ï¼‰
    this.sprite.alpha = this.initialAlpha * (1 - progress);

    // æ›´æ–°ç¼©æ”¾ï¼ˆç¼“æ…¢ç¼©å°åˆ° 20%ï¼‰
    this.sprite.scale.set(this.initialScale * (1 - progress * 0.8));

    return false;
  }

  /**
   * é‡ç½®ç²’å­ï¼ˆç”¨äºå¯¹è±¡æ± å›æ”¶ï¼‰
   */
  reset() {
    this.sprite.visible = false;
    this.velocity.x = 0;
    this.velocity.y = 0;
    this.acceleration.x = 0;
    this.acceleration.y = 0;
    this.age = 0;
    this.isAlive = false;
  }
}

export default Particle;
