import { Particle } from './Particle.js';

/**
 * ç²’å­å‘å°„å™¨ç±»
 */
export class ParticleEmitter {
  /**
   * åˆ›å»ºç²’å­å‘å°„å™¨
   * @param {Object} config - å‘å°„å™¨é…ç½®
   */
  constructor(config) {
    this.config = config;
    this.particles = [];
    this.isActive = true;
    this.age = 0;
  }

  /**
   * å‘å°„ç²’å­
   * @param {number} count - ç²’å­æ•°é‡
   * @param {Function} particleFactory - ç²’å­å·¥å‚å‡½æ•°
   */
  emit(count, particleFactory) {
    for (let i = 0; i < count; i++) {
      const particle = particleFactory();
      if (particle) {
        this.particles.push(particle);
      }
    }
  }

  /**
   * æ›´æ–°æ‰€æœ‰ç²’å­
   * @param {number} deltaTime - å¸§é—´éš”æ—¶é—´ï¼ˆç§’ï¼‰
   * @returns {boolean} æ˜¯å¦æ‰€æœ‰ç²’å­éƒ½å·²æ­»äº¡
   */
  update(deltaTime) {
    if (!this.isActive) return true;

    this.age += deltaTime;

    // æ›´æ–°æ‰€æœ‰ç²’å­ï¼Œç§»é™¤æ­»äº¡çš„ç²’å­
    for (let i = this.particles.length - 1; i >= 0; i--) {
      const particle = this.particles[i];
      const isDead = particle.update(deltaTime);

      if (isDead) {
        this.particles.splice(i, 1);
      }
    }

    // å¦‚æœæ‰€æœ‰ç²’å­éƒ½æ­»äº¡ï¼Œæ ‡è®°å‘å°„å™¨ä¸ºä¸æ´»è·ƒ
    if (this.particles.length === 0) {
      this.isActive = false;
      return true;
    }

    return false;
  }

  /**
   * é”€æ¯å‘å°„å™¨
   */
  destroy() {
    this.particles.forEach(particle => particle.reset());
    this.particles = [];
    this.isActive = false;
  }

  /**
   * è·å–æ´»è·ƒç²’å­æ•°é‡
   */
  getActiveCount() {
    return this.particles.length;
  }
}

export default ParticleEmitter;
