/**
 * äº‹ä»¶æ€»çº¿ - å®ç°å‘å¸ƒ-è®¢é˜…æ¨¡å¼
 * ç”¨äºæ¨¡å—é—´çš„æ¾è€¦åˆé€šä¿¡
 */

// æ¸¸æˆäº‹ä»¶å¸¸é‡
export const GameEvents = {
  // æ¸¸æˆç”Ÿå‘½å‘¨æœŸäº‹ä»¶
  GAME_INIT: 'game:init',
  GAME_START: 'game:start',
  GAME_RESET: 'game:reset',
  GAME_OVER: 'game:over',
  
  // å›¾æ ‡äº‹ä»¶
  TILE_SELECT: 'tile:select',
  TILE_DESELECT: 'tile:deselect',
  TILE_SWAP_START: 'tile:swap:start',
  TILE_SWAP_COMPLETE: 'tile:swap:complete',
  TILE_SWAP_REVERT: 'tile:swap:revert',
  TILE_REMOVE_START: 'tile:remove:start',
  TILE_REMOVE_COMPLETE: 'tile:remove:complete',
  TILE_FALL_START: 'tile:fall:start',
  TILE_FALL_COMPLETE: 'tile:fall:complete',
  TILE_SPAWN_START: 'tile:spawn:start',
  TILE_SPAWN_COMPLETE: 'tile:spawn:complete',
  
  // åŒ¹é…äº‹ä»¶
  MATCH_FOUND: 'match:found',
  MATCH_NONE: 'match:none',
  
  // åˆ†æ•°å’Œè¿é”äº‹ä»¶
  SCORE_UPDATE: 'score:update',
  COMBO_TRIGGER: 'combo:trigger',
  
  // çŠ¶æ€äº‹ä»¶
  STATE_CHANGE: 'state:change',
  
  // åŠ¨ç”»äº‹ä»¶
  ANIMATION_START: 'animation:start',
  ANIMATION_COMPLETE: 'animation:complete',
  ANIMATION_QUEUE_EMPTY: 'animation:queue:empty',
  
  // è¾“å…¥äº‹ä»¶
  INPUT_ENABLED: 'input:enabled',
  INPUT_DISABLED: 'input:disabled',
  
  // æ¸¸æˆæ¿äº‹ä»¶
  BOARD_STABLE: 'board:stable',
  BOARD_SHUFFLE: 'board:shuffle',
  
  // ç§»åŠ¨æ£€æµ‹äº‹ä»¶
  MOVES_NONE: 'moves:none',
  
  // è®¡æ—¶å™¨äº‹ä»¶
  TIMER_UPDATE: 'timer:update',
  TIMER_WARNING: 'timer:warning',
  
  // é”™è¯¯äº‹ä»¶
  ERROR: 'error'
};

/**
 * äº‹ä»¶æ€»çº¿ç±»
 */
export class EventBus {
  constructor() {
    // å­˜å‚¨äº‹ä»¶ç›‘å¬å™¨çš„ Map: eventName -> Set<callback>
    this.listeners = new Map();
    
    // å­˜å‚¨ä¸€æ¬¡æ€§ç›‘å¬å™¨çš„ Set
    this.onceListeners = new Set();
    
    // è°ƒè¯•æ¨¡å¼
    this.debug = false;
  }

  /**
   * è®¢é˜…äº‹ä»¶
   * @param {string} eventName - äº‹ä»¶åç§°
   * @param {Function} callback - å›è°ƒå‡½æ•°
   * @returns {Function} å–æ¶ˆè®¢é˜…å‡½æ•°
   */
  on(eventName, callback) {
    // éªŒè¯å‚æ•°
    if (typeof eventName !== 'string' || !eventName) {
      throw new Error('äº‹ä»¶åç§°å¿…é¡»æ˜¯éç©ºå­—ç¬¦ä¸²');
    }
    
    if (typeof callback !== 'function') {
      throw new Error('å›è°ƒå¿…é¡»æ˜¯å‡½æ•°');
    }

    // å¦‚æœäº‹ä»¶ä¸å­˜åœ¨ï¼Œåˆ›å»ºæ–°çš„ Set
    if (!this.listeners.has(eventName)) {
      this.listeners.set(eventName, new Set());
    }

    // æ·»åŠ å›è°ƒåˆ°ç›‘å¬å™¨é›†åˆ
    this.listeners.get(eventName).add(callback);

    if (this.debug) {
      console.log(`[EventBus] è®¢é˜…äº‹ä»¶: ${eventName}`);
    }

    // è¿”å›å–æ¶ˆè®¢é˜…å‡½æ•°
    return () => this.off(eventName, callback);
  }

  /**
   * å–æ¶ˆè®¢é˜…äº‹ä»¶
   * @param {string} eventName - äº‹ä»¶åç§°
   * @param {Function} callback - å›è°ƒå‡½æ•°
   */
  off(eventName, callback) {
    if (!this.listeners.has(eventName)) {
      return;
    }

    const callbacks = this.listeners.get(eventName);
    callbacks.delete(callback);

    // å¦‚æœæ²¡æœ‰ç›‘å¬å™¨äº†ï¼Œåˆ é™¤äº‹ä»¶
    if (callbacks.size === 0) {
      this.listeners.delete(eventName);
    }

    // ä»ä¸€æ¬¡æ€§ç›‘å¬å™¨é›†åˆä¸­ç§»é™¤
    this.onceListeners.delete(callback);

    if (this.debug) {
      console.log(`[EventBus] å–æ¶ˆè®¢é˜…äº‹ä»¶: ${eventName}`);
    }
  }

  /**
   * ä¸€æ¬¡æ€§è®¢é˜…äº‹ä»¶ï¼ˆè§¦å‘ä¸€æ¬¡åè‡ªåŠ¨å–æ¶ˆè®¢é˜…ï¼‰
   * @param {string} eventName - äº‹ä»¶åç§°
   * @param {Function} callback - å›è°ƒå‡½æ•°
   * @returns {Function} å–æ¶ˆè®¢é˜…å‡½æ•°
   */
  once(eventName, callback) {
    // éªŒè¯å‚æ•°
    if (typeof eventName !== 'string' || !eventName) {
      throw new Error('äº‹ä»¶åç§°å¿…é¡»æ˜¯éç©ºå­—ç¬¦ä¸²');
    }
    
    if (typeof callback !== 'function') {
      throw new Error('å›è°ƒå¿…é¡»æ˜¯å‡½æ•°');
    }

    // åˆ›å»ºåŒ…è£…å‡½æ•°
    const wrappedCallback = (data) => {
      // æ‰§è¡ŒåŸå§‹å›è°ƒ
      callback(data);
      // è‡ªåŠ¨å–æ¶ˆè®¢é˜…
      this.off(eventName, wrappedCallback);
    };

    // æ ‡è®°ä¸ºä¸€æ¬¡æ€§ç›‘å¬å™¨
    this.onceListeners.add(wrappedCallback);

    // è®¢é˜…äº‹ä»¶
    return this.on(eventName, wrappedCallback);
  }

  /**
   * å‘å¸ƒäº‹ä»¶
   * @param {string} eventName - äº‹ä»¶åç§°
   * @param {*} data - äº‹ä»¶æ•°æ®
   */
  emit(eventName, data = null) {
    if (this.debug) {
      console.log(`[EventBus] å‘å¸ƒäº‹ä»¶: ${eventName}`, data);
    }

    // å¦‚æœæ²¡æœ‰ç›‘å¬å™¨ï¼Œç›´æ¥è¿”å›
    if (!this.listeners.has(eventName)) {
      return;
    }

    // è·å–æ‰€æœ‰ç›‘å¬å™¨å¹¶æ‰§è¡Œ
    const callbacks = this.listeners.get(eventName);
    
    // ä½¿ç”¨ Array.from åˆ›å»ºå‰¯æœ¬ï¼Œé¿å…åœ¨å›è°ƒä¸­ä¿®æ”¹ç›‘å¬å™¨é›†åˆå¯¼è‡´é—®é¢˜
    Array.from(callbacks).forEach(callback => {
      try {
        callback(data);
      } catch (error) {
        console.error(`[EventBus] äº‹ä»¶å¤„ç†é”™è¯¯ (${eventName}):`, error);
        // å‘å¸ƒé”™è¯¯äº‹ä»¶
        if (eventName !== GameEvents.ERROR) {
          this.emit(GameEvents.ERROR, {
            type: 'EVENT_HANDLER_ERROR',
            message: `äº‹ä»¶ ${eventName} çš„å¤„ç†å™¨æŠ›å‡ºé”™è¯¯`,
            error,
            eventName,
            data
          });
        }
      }
    });
  }

  /**
   * æ¸…é™¤æ‰€æœ‰ç›‘å¬å™¨
   */
  clear() {
    this.listeners.clear();
    this.onceListeners.clear();
    
    if (this.debug) {
      console.log('[EventBus] æ¸…é™¤æ‰€æœ‰ç›‘å¬å™¨');
    }
  }

  /**
   * æ¸…é™¤æŒ‡å®šäº‹ä»¶çš„æ‰€æœ‰ç›‘å¬å™¨
   * @param {string} eventName - äº‹ä»¶åç§°
   */
  clearEvent(eventName) {
    if (this.listeners.has(eventName)) {
      this.listeners.delete(eventName);
      
      if (this.debug) {
        console.log(`[EventBus] æ¸…é™¤äº‹ä»¶ç›‘å¬å™¨: ${eventName}`);
      }
    }
  }

  /**
   * è·å–æŒ‡å®šäº‹ä»¶çš„ç›‘å¬å™¨æ•°é‡
   * @param {string} eventName - äº‹ä»¶åç§°
   * @returns {number} ç›‘å¬å™¨æ•°é‡
   */
  listenerCount(eventName) {
    if (!this.listeners.has(eventName)) {
      return 0;
    }
    return this.listeners.get(eventName).size;
  }

  /**
   * è·å–æ‰€æœ‰äº‹ä»¶åç§°
   * @returns {string[]} äº‹ä»¶åç§°æ•°ç»„
   */
  eventNames() {
    return Array.from(this.listeners.keys());
  }

  /**
   * å¯ç”¨è°ƒè¯•æ¨¡å¼
   */
  enableDebug() {
    this.debug = true;
    console.log('[EventBus] è°ƒè¯•æ¨¡å¼å·²å¯ç”¨');
  }

  /**
   * ç¦ç”¨è°ƒè¯•æ¨¡å¼
   */
  disableDebug() {
    this.debug = false;
  }
}

// å¯¼å‡ºå•ä¾‹å®ä¾‹
export const eventBus = new EventBus();

export default EventBus;
