/**
 * æ¸¸æˆé”™è¯¯ç±»
 */
export class GameError extends Error {
  constructor(type, message, details = null) {
    super(message);
    this.name = 'GameError';
    this.type = type;
    this.details = details;
    this.timestamp = Date.now();
  }
}

/**
 * é”™è¯¯ç±»å‹å¸¸é‡
 */
export const ErrorType = {
  INIT_ERROR: 'INIT_ERROR',           // åˆå§‹åŒ–é”™è¯¯
  LOGIC_ERROR: 'LOGIC_ERROR',         // æ¸¸æˆé€»è¾‘é”™è¯¯
  RENDER_ERROR: 'RENDER_ERROR',       // æ¸²æŸ“é”™è¯¯
  ANIMATION_ERROR: 'ANIMATION_ERROR', // åŠ¨ç”»é”™è¯¯
  RESOURCE_ERROR: 'RESOURCE_ERROR',   // èµ„æºåŠ è½½é”™è¯¯
  CONFIG_ERROR: 'CONFIG_ERROR',       // é…ç½®é”™è¯¯
  CONTEXT_LOST: 'CONTEXT_LOST'        // WebGLä¸Šä¸‹æ–‡ä¸¢å¤±
};

/**
 * é”™è¯¯å¤„ç†å™¨ç±»
 */
export class ErrorHandler {
  constructor(eventBus = null) {
    this.eventBus = eventBus;
    this.errorLog = [];
    this.maxLogSize = 50;
    this.isRecovering = false;
  }

  /**
   * å¤„ç†é”™è¯¯
   * @param {GameError|Error} error - é”™è¯¯å¯¹è±¡
   * @param {Object} context - é”™è¯¯ä¸Šä¸‹æ–‡
   */
  handle(error, context = {}) {
    // è®°å½•é”™è¯¯
    this.logError(error, context);

    // æ ¹æ®é”™è¯¯ç±»å‹å¤„ç†
    if (error instanceof GameError) {
      this.handleGameError(error, context);
    } else {
      this.handleGenericError(error, context);
    }

    // å‘å¸ƒé”™è¯¯äº‹ä»¶
    if (this.eventBus) {
      this.eventBus.emit('error', {
        type: error.type || 'UNKNOWN_ERROR',
        message: error.message,
        error,
        context
      });
    }
  }

  /**
   * å¤„ç†æ¸¸æˆé”™è¯¯
   * @param {GameError} error - æ¸¸æˆé”™è¯¯
   * @param {Object} context - é”™è¯¯ä¸Šä¸‹æ–‡
   */
  handleGameError(error, context) {
    console.error(`[${error.type}] ${error.message}`, error.details || '');

    switch (error.type) {
      case ErrorType.INIT_ERROR:
        this.handleInitError(error, context);
        break;

      case ErrorType.LOGIC_ERROR:
        this.handleLogicError(error, context);
        break;

      case ErrorType.RENDER_ERROR:
        this.handleRenderError(error, context);
        break;

      case ErrorType.ANIMATION_ERROR:
        this.handleAnimationError(error, context);
        break;

      case ErrorType.RESOURCE_ERROR:
        this.handleResourceError(error, context);
        break;

      case ErrorType.CONFIG_ERROR:
        this.handleConfigError(error, context);
        break;

      case ErrorType.CONTEXT_LOST:
        this.handleContextLost(error, context);
        break;

      default:
        this.showErrorMessage('å‘ç”ŸæœªçŸ¥é”™è¯¯ï¼Œè¯·åˆ·æ–°é¡µé¢é‡è¯•');
    }
  }

  /**
   * å¤„ç†é€šç”¨é”™è¯¯
   * @param {Error} error - é”™è¯¯å¯¹è±¡
   * @param {Object} context - é”™è¯¯ä¸Šä¸‹æ–‡
   */
  handleGenericError(error, context) {
    console.error('Unexpected error:', error);
    this.showErrorMessage('å‘ç”Ÿæ„å¤–é”™è¯¯ï¼Œè¯·åˆ·æ–°é¡µé¢é‡è¯•');
  }

  /**
   * å¤„ç†åˆå§‹åŒ–é”™è¯¯
   */
  handleInitError(error, context) {
    this.showErrorMessage(
      'æ¸¸æˆåˆå§‹åŒ–å¤±è´¥',
      error.message,
      [{ text: 'åˆ·æ–°é¡µé¢', action: () => window.location.reload() }]
    );
  }

  /**
   * å¤„ç†é€»è¾‘é”™è¯¯
   */
  handleLogicError(error, context) {
    console.warn('Logic error occurred, attempting to recover...');
    
    // å°è¯•æ¢å¤æ¸¸æˆçŠ¶æ€
    if (context.gameEngine && !this.isRecovering) {
      this.isRecovering = true;
      try {
        context.gameEngine.reset();
        this.showErrorMessage('æ¸¸æˆçŠ¶æ€å¼‚å¸¸ï¼Œå·²è‡ªåŠ¨é‡ç½®', '', [], 2000);
      } catch (e) {
        console.error('Failed to recover:', e);
        this.showErrorMessage('æ— æ³•æ¢å¤æ¸¸æˆçŠ¶æ€ï¼Œè¯·åˆ·æ–°é¡µé¢');
      } finally {
        this.isRecovering = false;
      }
    }
  }

  /**
   * å¤„ç†æ¸²æŸ“é”™è¯¯
   */
  handleRenderError(error, context) {
    console.warn('Render error occurred, attempting to recover...');
    
    // å°è¯•é‡æ–°åˆå§‹åŒ–æ¸²æŸ“å™¨
    if (context.renderEngine && !this.isRecovering) {
      this.isRecovering = true;
      try {
        // ç®€å•çš„æ¢å¤ç­–ç•¥ï¼šæ ‡è®°éœ€è¦é‡ç»˜
        if (context.renderEngine.markDirty) {
          context.renderEngine.markDirty();
        }
        this.showErrorMessage('æ¸²æŸ“å¼‚å¸¸ï¼Œå·²å°è¯•æ¢å¤', '', [], 2000);
      } catch (e) {
        console.error('Failed to recover render engine:', e);
        this.showErrorMessage('æ¸²æŸ“å¼•æ“æ•…éšœï¼Œè¯·åˆ·æ–°é¡µé¢');
      } finally {
        this.isRecovering = false;
      }
    }
  }

  /**
   * å¤„ç†åŠ¨ç”»é”™è¯¯
   */
  handleAnimationError(error, context) {
    console.warn('Animation error occurred, skipping animation...');
    
    // åŠ¨ç”»é”™è¯¯é€šå¸¸ä¸è‡´å‘½ï¼Œè·³è¿‡å³å¯
    if (context.animationController) {
      try {
        context.animationController.stopAll();
      } catch (e) {
        console.error('Failed to stop animations:', e);
      }
    }
  }

  /**
   * å¤„ç†èµ„æºåŠ è½½é”™è¯¯
   */
  handleResourceError(error, context) {
    this.showErrorMessage(
      'èµ„æºåŠ è½½å¤±è´¥',
      error.message,
      [
        { text: 'é‡è¯•', action: () => window.location.reload() },
        { text: 'å–æ¶ˆ', action: () => {} }
      ]
    );
  }

  /**
   * å¤„ç†é…ç½®é”™è¯¯
   */
  handleConfigError(error, context) {
    this.showErrorMessage(
      'é…ç½®é”™è¯¯',
      error.message,
      [{ text: 'ç¡®å®š', action: () => window.location.reload() }]
    );
  }

  /**
   * å¤„ç†WebGLä¸Šä¸‹æ–‡ä¸¢å¤±
   */
  handleContextLost(error, context) {
    console.error('WebGL context lost');
    
    this.showErrorMessage(
      'WebGLä¸Šä¸‹æ–‡ä¸¢å¤±',
      'å¯èƒ½æ˜¯GPUèµ„æºä¸è¶³æˆ–é©±åŠ¨é—®é¢˜',
      [
        { text: 'åˆ·æ–°é¡µé¢', action: () => window.location.reload() },
        { text: 'ç¨åé‡è¯•', action: () => {} }
      ]
    );

    // å°è¯•æ¢å¤ä¸Šä¸‹æ–‡
    if (context.renderEngine && context.renderEngine.app) {
      const canvas = context.renderEngine.app.canvas;
      if (canvas) {
        canvas.addEventListener('webglcontextrestored', () => {
          console.log('WebGL context restored');
          window.location.reload();
        }, { once: true });
      }
    }
  }

  /**
   * æ˜¾ç¤ºé”™è¯¯æ¶ˆæ¯
   * @param {string} title - é”™è¯¯æ ‡é¢˜
   * @param {string} message - é”™è¯¯æ¶ˆæ¯
   * @param {Array} buttons - æŒ‰é’®é…ç½®
   * @param {number} autoClose - è‡ªåŠ¨å…³é—­æ—¶é—´ï¼ˆæ¯«ç§’ï¼‰
   */
  showErrorMessage(title, message = '', buttons = [], autoClose = 0) {
    // åˆ›å»ºé”™è¯¯æç¤ºUI
    const overlay = document.createElement('div');
    overlay.className = 'error-overlay';
    overlay.style.cssText = `
      position: fixed;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      background: rgba(0, 0, 0, 0.8);
      display: flex;
      align-items: center;
      justify-content: center;
      z-index: 10000;
    `;

    const dialog = document.createElement('div');
    dialog.className = 'error-dialog';
    dialog.style.cssText = `
      background: #2c3e50;
      color: #ecf0f1;
      padding: 30px;
      border-radius: 10px;
      max-width: 400px;
      text-align: center;
      box-shadow: 0 4px 20px rgba(0, 0, 0, 0.5);
    `;

    const titleEl = document.createElement('h2');
    titleEl.textContent = title;
    titleEl.style.cssText = `
      margin: 0 0 15px 0;
      color: #e74c3c;
      font-size: 24px;
    `;

    const messageEl = document.createElement('p');
    messageEl.textContent = message;
    messageEl.style.cssText = `
      margin: 0 0 20px 0;
      color: #bdc3c7;
      font-size: 16px;
      line-height: 1.5;
    `;

    dialog.appendChild(titleEl);
    if (message) {
      dialog.appendChild(messageEl);
    }

    // æ·»åŠ æŒ‰é’®
    if (buttons.length > 0) {
      const buttonContainer = document.createElement('div');
      buttonContainer.style.cssText = `
        display: flex;
        gap: 10px;
        justify-content: center;
      `;

      buttons.forEach(btn => {
        const button = document.createElement('button');
        button.textContent = btn.text;
        button.style.cssText = `
          padding: 10px 20px;
          background: #3498db;
          color: white;
          border: none;
          border-radius: 5px;
          cursor: pointer;
          font-size: 16px;
          transition: background 0.3s;
        `;
        button.onmouseover = () => button.style.background = '#2980b9';
        button.onmouseout = () => button.style.background = '#3498db';
        button.onclick = () => {
          document.body.removeChild(overlay);
          if (btn.action) btn.action();
        };
        buttonContainer.appendChild(button);
      });

      dialog.appendChild(buttonContainer);
    }

    overlay.appendChild(dialog);
    document.body.appendChild(overlay);

    // è‡ªåŠ¨å…³é—­
    if (autoClose > 0) {
      setTimeout(() => {
        if (document.body.contains(overlay)) {
          document.body.removeChild(overlay);
        }
      }, autoClose);
    }
  }

  /**
   * è®°å½•é”™è¯¯
   * @param {Error} error - é”™è¯¯å¯¹è±¡
   * @param {Object} context - é”™è¯¯ä¸Šä¸‹æ–‡
   */
  logError(error, context) {
    const logEntry = {
      timestamp: Date.now(),
      type: error.type || 'UNKNOWN',
      message: error.message,
      stack: error.stack,
      context
    };

    this.errorLog.push(logEntry);

    // é™åˆ¶æ—¥å¿—å¤§å°
    if (this.errorLog.length > this.maxLogSize) {
      this.errorLog.shift();
    }
  }

  /**
   * è·å–é”™è¯¯æ—¥å¿—
   * @returns {Array} é”™è¯¯æ—¥å¿—
   */
  getErrorLog() {
    return [...this.errorLog];
  }

  /**
   * æ¸…é™¤é”™è¯¯æ—¥å¿—
   */
  clearErrorLog() {
    this.errorLog = [];
  }

  /**
   * éªŒè¯é…ç½®
   * @param {Object} config - é…ç½®å¯¹è±¡
   * @throws {GameError} é…ç½®é”™è¯¯
   */
  static validateConfig(config) {
    const required = ['board', 'rendering', 'animation', 'scoring', 'timer'];
    
    for (const key of required) {
      if (!config[key]) {
        throw new GameError(
          ErrorType.CONFIG_ERROR,
          `ç¼ºå°‘å¿…éœ€çš„é…ç½®é¡¹: ${key}`
        );
      }
    }

    // éªŒè¯æ¸¸æˆæ¿é…ç½®
    if (config.board.rows < 4 || config.board.rows > 20) {
      throw new GameError(
        ErrorType.CONFIG_ERROR,
        'æ¸¸æˆæ¿è¡Œæ•°å¿…é¡»åœ¨4-20ä¹‹é—´'
      );
    }

    if (config.board.cols < 4 || config.board.cols > 20) {
      throw new GameError(
        ErrorType.CONFIG_ERROR,
        'æ¸¸æˆæ¿åˆ—æ•°å¿…é¡»åœ¨4-20ä¹‹é—´'
      );
    }

    if (config.board.tileTypes < 3 || config.board.tileTypes > 10) {
      throw new GameError(
        ErrorType.CONFIG_ERROR,
        'å›¾æ ‡ç±»å‹æ•°é‡å¿…é¡»åœ¨3-10ä¹‹é—´'
      );
    }

    // éªŒè¯æ¸²æŸ“é…ç½®
    if (config.rendering.tileSize < 32 || config.rendering.tileSize > 128) {
      throw new GameError(
        ErrorType.CONFIG_ERROR,
        'å›¾æ ‡å°ºå¯¸å¿…é¡»åœ¨32-128ä¹‹é—´'
      );
    }

    return true;
  }
}

export default ErrorHandler;
