/**
 * æ¸¸æˆæ¿ç®¡ç†å™¨ - ç®¡ç†æ¸¸æˆæ¿çš„æ•°æ®ç»“æ„å’ŒåŸºæœ¬æ“ä½œ
 */

import { Tile } from './Tile.js';

/**
 * æ¸¸æˆæ¿ç®¡ç†å™¨ç±»
 */
export class BoardManager {
  /**
   * åˆ›å»ºæ¸¸æˆæ¿ç®¡ç†å™¨
   * @param {number} rows - è¡Œæ•°
   * @param {number} cols - åˆ—æ•°
   * @param {number} tileTypes - å›¾æ ‡ç±»å‹æ•°é‡
   * @param {Object} matchDetector - åŒ¹é…æ£€æµ‹å™¨å®ä¾‹ï¼ˆå¯é€‰ï¼Œç”¨äºåˆå§‹åŒ–æ£€æµ‹ï¼‰
   */
  constructor(rows, cols, tileTypes, matchDetector = null) {
    this.rows = rows;
    this.cols = cols;
    this.tileTypes = tileTypes;
    this.matchDetector = matchDetector;
    this.grid = [];  // äºŒç»´æ•°ç»„å­˜å‚¨Tile
  }

  /**
   * åˆ›å»ºæ¸¸æˆæ¿å¹¶éšæœºå¡«å……å›¾æ ‡
   * @returns {Tile[][]} æ¸¸æˆæ¿ç½‘æ ¼
   */
  createBoard() {
    this.grid = [];
    
    for (let y = 0; y < this.rows; y++) {
      this.grid[y] = [];
      for (let x = 0; x < this.cols; x++) {
        const type = this._getRandomType();
        this.grid[y][x] = new Tile(type, x, y);
      }
    }

    return this.grid;
  }

  /**
   * è·å–éšæœºå›¾æ ‡ç±»å‹
   * @returns {number} å›¾æ ‡ç±»å‹ (0 åˆ° tileTypes-1)
   */
  _getRandomType() {
    return Math.floor(Math.random() * this.tileTypes);
  }

  /**
   * è·å–æŒ‡å®šä½ç½®çš„å›¾æ ‡
   * @param {number} x - Xåæ ‡
   * @param {number} y - Yåæ ‡
   * @returns {Tile|null} å›¾æ ‡å¯¹è±¡ï¼Œå¦‚æœä½ç½®æ— æ•ˆè¿”å›null
   */
  getTile(x, y) {
    if (!this.isValidPosition(x, y)) {
      return null;
    }
    return this.grid[y][x];
  }

  /**
   * è®¾ç½®æŒ‡å®šä½ç½®çš„å›¾æ ‡
   * @param {number} x - Xåæ ‡
   * @param {number} y - Yåæ ‡
   * @param {Tile} tile - å›¾æ ‡å¯¹è±¡
   */
  setTile(x, y, tile) {
    if (!this.isValidPosition(x, y)) {
      console.warn(`å°è¯•åœ¨æ— æ•ˆä½ç½®è®¾ç½®å›¾æ ‡: (${x}, ${y})`);
      return;
    }
    
    if (tile) {
      tile.setPosition(x, y);
    }
    
    this.grid[y][x] = tile;
  }

  /**
   * æ£€æŸ¥ä½ç½®æ˜¯å¦æœ‰æ•ˆ
   * @param {number} x - Xåæ ‡
   * @param {number} y - Yåæ ‡
   * @returns {boolean} æ˜¯å¦æœ‰æ•ˆ
   */
  isValidPosition(x, y) {
    return x >= 0 && x < this.cols && y >= 0 && y < this.rows;
  }

  /**
   * æ£€æŸ¥ä¸¤ä¸ªä½ç½®æ˜¯å¦ç›¸é‚»ï¼ˆæ°´å¹³æˆ–å‚ç›´ï¼‰
   * @param {Object} pos1 - ä½ç½®1 {x, y}
   * @param {Object} pos2 - ä½ç½®2 {x, y}
   * @returns {boolean} æ˜¯å¦ç›¸é‚»
   */
  isAdjacent(pos1, pos2) {
    const dx = Math.abs(pos1.x - pos2.x);
    const dy = Math.abs(pos1.y - pos2.y);
    
    // ç›¸é‚»æ¡ä»¶ï¼šæ¨ªå‘æˆ–çºµå‘è·ç¦»ä¸º1ï¼Œä¸”å¦ä¸€æ–¹å‘è·ç¦»ä¸º0
    return (dx === 1 && dy === 0) || (dx === 0 && dy === 1);
  }

  /**
   * äº¤æ¢ä¸¤ä¸ªå›¾æ ‡çš„ä½ç½®
   * @param {Object} pos1 - ä½ç½®1 {x, y}
   * @param {Object} pos2 - ä½ç½®2 {x, y}
   * @returns {boolean} æ˜¯å¦äº¤æ¢æˆåŠŸ
   */
  swapTiles(pos1, pos2) {
    if (!this.isValidPosition(pos1.x, pos1.y) || !this.isValidPosition(pos2.x, pos2.y)) {
      return false;
    }

    const tile1 = this.getTile(pos1.x, pos1.y);
    const tile2 = this.getTile(pos2.x, pos2.y);

    if (!tile1 || !tile2) {
      return false;
    }

    // äº¤æ¢ä½ç½®
    this.setTile(pos1.x, pos1.y, tile2);
    this.setTile(pos2.x, pos2.y, tile1);

    return true;
  }

  /**
   * ç§»é™¤æŒ‡å®šä½ç½®çš„å›¾æ ‡
   * @param {Array<{x, y}>} positions - ä½ç½®æ•°ç»„
   */
  removeTiles(positions) {
    positions.forEach(pos => {
      if (this.isValidPosition(pos.x, pos.y)) {
        this.grid[pos.y][pos.x] = null;
      }
    });
  }

  /**
   * è·å–æ‰€æœ‰ç©ºä½ç½®
   * @returns {Array<{x, y}>} ç©ºä½ç½®æ•°ç»„
   */
  getEmptyPositions() {
    const emptyPositions = [];
    
    for (let y = 0; y < this.rows; y++) {
      for (let x = 0; x < this.cols; x++) {
        if (this.grid[y][x] === null) {
          emptyPositions.push({ x, y });
        }
      }
    }
    
    return emptyPositions;
  }

  /**
   * åº”ç”¨é‡åŠ›ä½¿å›¾æ ‡ä¸‹è½
   * @returns {Array<{tile, from, to}>} ç§»åŠ¨è®°å½•æ•°ç»„
   */
  applyGravity() {
    const movements = [];

    // ä»ä¸‹å¾€ä¸Šéå†æ¯ä¸€åˆ—
    for (let x = 0; x < this.cols; x++) {
      let writeY = this.rows - 1;  // å†™å…¥ä½ç½®ï¼ˆä»åº•éƒ¨å¼€å§‹ï¼‰

      // ä»ä¸‹å¾€ä¸Šæ‰«æ
      for (let y = this.rows - 1; y >= 0; y--) {
        const tile = this.grid[y][x];
        
        if (tile !== null) {
          // å¦‚æœå½“å‰ä½ç½®ä¸æ˜¯å†™å…¥ä½ç½®ï¼Œè¯´æ˜éœ€è¦ä¸‹è½
          if (y !== writeY) {
            const from = { x, y };
            const to = { x, y: writeY };
            
            movements.push({ tile, from, to });
            
            // ç§»åŠ¨å›¾æ ‡
            this.grid[writeY][x] = tile;
            this.grid[y][x] = null;
            tile.setPosition(x, writeY);
          }
          
          writeY--;  // å†™å…¥ä½ç½®ä¸Šç§»
        }
      }
    }

    return movements;
  }

  /**
   * å¡«å……æ¸¸æˆæ¿ï¼ˆåœ¨é¡¶éƒ¨ç”Ÿæˆæ–°å›¾æ ‡ï¼‰
   * @returns {Array<Tile>} æ–°ç”Ÿæˆçš„å›¾æ ‡æ•°ç»„
   */
  fillBoard() {
    const newTiles = [];

    for (let x = 0; x < this.cols; x++) {
      for (let y = 0; y < this.rows; y++) {
        if (this.grid[y][x] === null) {
          const type = this._getRandomType();
          const tile = new Tile(type, x, y);
          this.grid[y][x] = tile;
          newTiles.push(tile);
        }
      }
    }

    return newTiles;
  }

  /**
   * ç¡®ä¿åˆå§‹åŒ–æ—¶æ— åŒ¹é…
   * ä½¿ç”¨æ™ºèƒ½æ›¿æ¢ç®—æ³•ï¼Œæœ€å¤šå°è¯•100æ¬¡
   * @returns {boolean} æ˜¯å¦æˆåŠŸæ¶ˆé™¤æ‰€æœ‰åˆå§‹åŒ¹é…
   */
  ensureNoInitialMatches() {
    if (!this.matchDetector) {
      console.warn('æœªæä¾›åŒ¹é…æ£€æµ‹å™¨ï¼Œè·³è¿‡åˆå§‹åŒ¹é…æ£€æµ‹');
      return true;
    }

    const maxAttempts = 100;
    let attempts = 0;

    while (attempts < maxAttempts) {
      const matches = this.matchDetector.findMatches(this);

      if (matches.length === 0) {
        return true;  // æˆåŠŸï¼šæ— åŒ¹é…
      }

      // ç­–ç•¥ï¼šåªæ›¿æ¢åŒ¹é…çš„å›¾æ ‡ï¼Œè€Œä¸æ˜¯é‡æ–°ç”Ÿæˆæ•´ä¸ªæ£‹ç›˜
      for (const match of matches) {
        for (const tile of match.tiles) {
          let newType;
          let safeType = false;

          // å°è¯•æ‰¾åˆ°ä¸€ä¸ªä¸ä¼šäº§ç”Ÿæ–°åŒ¹é…çš„ç±»å‹
          for (let i = 0; i < this.tileTypes; i++) {
            newType = i;
            tile.type = newType;

            // æ£€æŸ¥è¿™ä¸ªä½ç½®æ˜¯å¦è¿˜ä¼šäº§ç”ŸåŒ¹é…
            if (!this.wouldCreateMatch(tile.x, tile.y)) {
              safeType = true;
              break;
            }
          }

          // å¦‚æœæ‰€æœ‰ç±»å‹éƒ½ä¼šäº§ç”ŸåŒ¹é…ï¼Œéšæœºé€‰æ‹©ä¸€ä¸ª
          if (!safeType) {
            tile.type = this._getRandomType();
          }
        }
      }

      attempts++;
    }

    // å¦‚æœ100æ¬¡å°è¯•åä»æœ‰åŒ¹é…ï¼Œå¼ºåˆ¶é‡æ–°ç”Ÿæˆæ•´ä¸ªæ£‹ç›˜
    console.warn('100æ¬¡å°è¯•åä»æœ‰åˆå§‹åŒ¹é…ï¼Œé‡æ–°ç”Ÿæˆæ£‹ç›˜');
    this.createBoard();
    return this.ensureNoInitialMatches();
  }

  /**
   * æ£€æŸ¥æŒ‡å®šä½ç½®æ˜¯å¦ä¼šäº§ç”ŸåŒ¹é…
   * @param {number} x - Xåæ ‡
   * @param {number} y - Yåæ ‡
   * @returns {boolean} æ˜¯å¦ä¼šäº§ç”ŸåŒ¹é…
   */
  wouldCreateMatch(x, y) {
    const tile = this.getTile(x, y);
    // âœ… ç‰¹æ®Šå›¾æ ‡ä¸å‚ä¸åŒ¹é…
    if (!tile || tile.isSpecial) return false;

    // æ£€æŸ¥æ¨ªå‘
    let horizontalCount = 1;
    
    // å‘å·¦æ£€æŸ¥
    for (let i = x - 1; i >= 0; i--) {
      const leftTile = this.getTile(i, y);
      // âœ… è·³è¿‡ç‰¹æ®Šå›¾æ ‡
      if (leftTile && !leftTile.isSpecial && leftTile.type === tile.type) {
        horizontalCount++;
      } else {
        break;
      }
    }
    
    // å‘å³æ£€æŸ¥
    for (let i = x + 1; i < this.cols; i++) {
      const rightTile = this.getTile(i, y);
      // âœ… è·³è¿‡ç‰¹æ®Šå›¾æ ‡
      if (rightTile && !rightTile.isSpecial && rightTile.type === tile.type) {
        horizontalCount++;
      } else {
        break;
      }
    }
    
    if (horizontalCount >= 3) return true;

    // æ£€æŸ¥çºµå‘
    let verticalCount = 1;
    
    // å‘ä¸Šæ£€æŸ¥
    for (let i = y - 1; i >= 0; i--) {
      const upTile = this.getTile(x, i);
      // âœ… è·³è¿‡ç‰¹æ®Šå›¾æ ‡
      if (upTile && !upTile.isSpecial && upTile.type === tile.type) {
        verticalCount++;
      } else {
        break;
      }
    }
    
    // å‘ä¸‹æ£€æŸ¥
    for (let i = y + 1; i < this.rows; i++) {
      const downTile = this.getTile(x, i);
      // âœ… è·³è¿‡ç‰¹æ®Šå›¾æ ‡
      if (downTile && !downTile.isSpecial && downTile.type === tile.type) {
        verticalCount++;
      } else {
        break;
      }
    }
    
    if (verticalCount >= 3) return true;

    return false;
  }

  /**
   * æ´—ç‰Œæ¸¸æˆæ¿
   */
  shuffleBoard() {
    // æ”¶é›†æ‰€æœ‰å›¾æ ‡ç±»å‹
    const types = [];
    for (let y = 0; y < this.rows; y++) {
      for (let x = 0; x < this.cols; x++) {
        const tile = this.grid[y][x];
        if (tile) {
          types.push(tile.type);
        }
      }
    }

    // Fisher-Yates æ´—ç‰Œç®—æ³•
    for (let i = types.length - 1; i > 0; i--) {
      const j = Math.floor(Math.random() * (i + 1));
      [types[i], types[j]] = [types[j], types[i]];
    }

    // é‡æ–°åˆ†é…ç±»å‹
    let index = 0;
    for (let y = 0; y < this.rows; y++) {
      for (let x = 0; x < this.cols; x++) {
        const tile = this.grid[y][x];
        if (tile) {
          tile.type = types[index++];
        }
      }
    }

    // ç¡®ä¿æ´—ç‰Œåæ— åˆå§‹åŒ¹é…
    this.ensureNoInitialMatches();
  }

  /**
   * åˆ›å»ºç‰¹æ®Šå›¾æ ‡
   * @param {number} x - Xåæ ‡
   * @param {number} y - Yåæ ‡
   * @param {string} specialType - ç‰¹æ®Šå›¾æ ‡ç±»å‹
   * @returns {Tile|null} åˆ›å»ºçš„ç‰¹æ®Šå›¾æ ‡
   */
  createSpecialTile(x, y, specialType) {
    if (!this.isValidPosition(x, y)) {
      console.warn(`å°è¯•åœ¨æ— æ•ˆä½ç½®åˆ›å»ºç‰¹æ®Šå›¾æ ‡: (${x}, ${y})`);
      return null;
    }

    const tile = this.getTile(x, y);
    if (tile) {
      tile.setSpecial(specialType);
      console.log(`âœ¨ åˆ›å»ºç‰¹æ®Šå›¾æ ‡: ${specialType} at (${x}, ${y})`);
      return tile;
    }

    return null;
  }

  /**
   * å…‹éš†æ¸¸æˆæ¿ï¼ˆç”¨äºæ£€æµ‹ï¼‰
   * @returns {BoardManager} æ–°çš„æ¸¸æˆæ¿å®ä¾‹
   */
  clone() {
    const cloned = new BoardManager(this.rows, this.cols, this.tileTypes, this.matchDetector);
    cloned.grid = [];

    for (let y = 0; y < this.rows; y++) {
      cloned.grid[y] = [];
      for (let x = 0; x < this.cols; x++) {
        const tile = this.grid[y][x];
        cloned.grid[y][x] = tile ? tile.clone() : null;
      }
    }

    return cloned;
  }

  /**
   * è·å–æ¸¸æˆæ¿çš„å­—ç¬¦ä¸²è¡¨ç¤ºï¼ˆç”¨äºè°ƒè¯•ï¼‰
   * @returns {string}
   */
  toString() {
    let str = '\næ¸¸æˆæ¿çŠ¶æ€:\n';
    for (let y = 0; y < this.rows; y++) {
      for (let x = 0; x < this.cols; x++) {
        const tile = this.grid[y][x];
        str += tile ? tile.type : '-';
        str += ' ';
      }
      str += '\n';
    }
    return str;
  }
}

export default BoardManager;
